.home {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height:100vh;
}

h1 {
  text-align: center;
}

.division:before {
  content:'\00F7'
}

.division::after {
  content:'\00F7'
}

.math-name {
  font-size: 1.75rem;
}

.stats-button{
  border:1px white solid;
  border-radius: 3px;
  margin-top: 5rem;
  padding: .5rem .5rem;
  background-color: black;
  color:white;
  font-family: 'Gaegu', sans-serif;
  font-size: 1rem;
}

.stats-button:active {
  background-color: rgba(25, 25, 25, 1);
}

.stats-button:hover {
  cursor: pointer;
}



